<template>
  
  
  
  <svg :style = 'style' @click = 'log()'>
  <text
  fill = 'white'
  
  :dx = 'width - 120'
  :dy = 30
  font-size = 22
  font-weight = 'bold'>
    {{date_select}}
  </text>
  

  <g transform = 'translate(125,30)' cursor = 'pointer' @click = 'time_flag = !time_flag'>
    <polygon points = '0,13 25,0 0,-13' stroke = 'white' v-if = '!time_flag'>
    </polygon>
    <rect 
    y = -13
    x = -5
    width = 25
    height = 25
    v-if = 'time_flag'
    stroke = 'white'>
    </rect>
  </g>
  <g transform = 'translate(20,15)' @click = 'cumulated = !cumulated'
  cursor = 'pointer'>
  

  <rect
  
  stroke = 'white'
  height = 30
  width = 90
  fill = 'none'
  rx = 5
  >
  </rect>

  <text
  dy = 18
  dx = 45
  text-anchor = 'middle'
  fill = 'white'
  font-size = 16
  font-weight = 'bold'>
    {{cumulated?"cumulated" : "daily"}}
  </text>
  </g>
  <g id = 'background'>
    <path class = 'outer' v-for = '(d,i) in geo_data.outer.features'
      :key = 'i'
      fill = 'None'
      stroke = 'white'
      stroke-opacity = 0.3
      stroke-width = '0.5px'
      :d = 'map_path(d)'
      >
    </path>

    <path class = 'outer2' v-for = '(d,i) in geo_data.outer2.features'
      :key = 'i'
      fill = 'None'
      stroke = 'white'
      stroke-opacity = 0.3
      stroke-width = '0.5px'
      :d = 'map_path(d)'
      >
    </path>

    <path class = 'inner' v-for = '(d,i) in geo_data.inner.features'
      :key = 'i'
      fill = 'none'
      stroke = 'white'
      stroke-opacity = 0.7
      stroke-width = 2
      :d = 'map_path(d)'
      >
    </path>

    <text class = 'dis_label' v-for = '(d,i) in geo_data.inner.features'
      :key='i'
      fill = 'white'
      :dx = 'projection(d.properties.centroid)[0]'
      :dy = 'projection(d.properties.centroid)[1]'
      text-anchor = 'middle'
      font-size = 12
    >
    {{d.properties.name}}
    </text>

  </g>

  <g id = 'people' >
    <g >
      <circle class = 'person' v-for = '(d) in show_data'
      :key = 'd.id'
      :cx = 'projection(d.xy)[0]'
      :cy = 'projection(d.xy)[1]'
      :r = 'd.type == "无症状"? 3 : d.type == "轻型"? 5:8 '
      opacity = 0.7
      
      :fill = 'd.type == "无症状"? "#F6F7BB" : d.type == "轻型"? "#FFBF81":"#FF5D53" '
      >
      </circle>

    </g> 

  </g>

  </svg>
 
   
  <input type = 'range' min = 0  :max  = 'date_list.length - 1' v-model = 'date_index'/>

  <svg :style = 'style2' >
    <text :dx = 'width/4'
    text-anchor = 'middle'
    fill = 'white'
    font-size = 20
    dy = 30
    font-weight = 'bold'
    >Number of Infections: {{cumulated? 'cumulated': 'daily'}}</text>
    <g id = 'ranking'>
    
      
      
    </g>
  </svg>

  <svg :style = 'style3' id = 'svg3'>
    <g id = 'axis'>
      <line x1 = 30
      x2 = 30
      y1 = 25
      :y2 = ' 25 +  axis_config.height'
      stroke = 'white'>
      </line>
      <line
      x1 = 30
      x2 = 26
      y1 = 25
      y2 = 32
      stroke = 'white'>
      </line>

      <line
      x1 = 30
      x2 = 34
      y1 = 25
      y2 = 32
      stroke = 'white'>
      </line>

      <line
      :x1 = ' 30 + axis_config.width'
      :x2 = ' 23 + axis_config.width'
      :y1 = ' 25 +  axis_config.height'
      :y2 = ' 21 +  axis_config.height'
      stroke = 'white'>
      </line>

      <line
      :x1 = ' 30 + axis_config.width'
      :x2 = ' 23 + axis_config.width'
      :y1 = ' 25 +  axis_config.height'
      :y2 = ' 29 +  axis_config.height'
      stroke = 'white'>
      </line>

      <line x1 = 30
      :x2 = ' 30 + axis_config.width'
      :y1 = ' 25 +  axis_config.height'
      :y2 = ' 25 +  axis_config.height'
      stroke = 'white'>
      </line>
    </g>
    <g id = 'lines'>
      <path v-for = '(d,i) in line_data'
      :key = 'i'
      :d = 'line_g(d)'
      
      :stroke = 'color_scale2(i)'
      fill = 'none'
      >
      </path>
    </g>

    <g id = 'legend'>
      <g v-for = '(d,i) in type_list'
      :key = 'i' 
      :transform = '`translate(`+ (i * axis_config.width /5 + 130)  + `,10)`'>
        <rect 
        height = 10
        width = 10
        :fill = 'color_scale2(i)'
        rx = 3
        >
        </rect>
        <text
        fill = 'white'
        font-size = 12
        text-anchor = 'start'
        dx = 15
        dy = 9>
          {{d}}
        </text>
      </g>
    </g>
    <rect id = 'move'
    fill = 'black'
    :x = '30 + date_index * axis_config.width / (date_list.length-1)'
    y = 33
    :height = 'axis_config.height-13'
    width = 1000>
    </rect>
  </svg>

</template>

<script>
import * as d3 from 'd3'
export default {
  name: 'maps',
  data(){
    return {
      width:document.documentElement.clientWidth * 2/3,
      height:document.documentElement.clientHeight-20,
      geo_data:{
        inner:{},
        outer:{},
        outer2:{},
      },
      event_data:[],
      date_index : 0,
      cumulated:true,
      time_flag : false,
      timer : 0,
      color_scale : d3.scale.category20c(),
      color_scale2 : d3.scale.category20(),
      type_list:['无症状','轻型','普通型'],
      
      
      
      
    }
  },
  mounted(){

    d3.json("北京市.json",d=>{
      this.geo_data.inner = d
    })

    d3.json("河北省.json",d=>{
      this.geo_data.outer = d
    })

    d3.json("天津市.json",d=>{
      this.geo_data.outer2 = d
    })

    d3.csv("event.csv",d=>{
      this.event_data = d
    })

    
    
  },
  computed:{
   
    line_data(){
      let ret = []
      let type_lst = ['无症状','轻型','普通型']
      for(let i = 0;i<type_lst.length;i++){
        let tmp_lst = []
        for(let j = 0;j<this.date_list.length;j++){
          let tmp = new Object()
          tmp.x = j
          let count = this.daily.filter(d=>d.date == this.date_list[j] && d.type == type_lst[i]).length

          tmp.y = count
          tmp_lst.push(tmp)
        }
        ret.push(tmp_lst)
      }
      //return ret
      return d3.layout.stack()
				.offset(0)(ret)
     // return ret
    },
    line_g(){
      var xS = d3.scale.linear().domain([0,this.date_list.length - 1]).range([30,this.axis_config.width + 30]);
			var yS = d3.scale.linear().domain([100,-10]).range([25,this.axis_config.height + 25]);
      return d3.svg.line()
			.interpolate("basis")
			.x(function(d){return xS(d.x);})
			.y(function(d){return yS(d.y+d.y0);});
    },
    axis_config(){
      return {
        width:this.width/2 - 25 -60,
        height:this.height/3 - 5 - 50 ,
      }
    },
    watcher(){
      return this.bar_data.length
    },
    rank(){
      let ret = []
      for(let i = 0;i<this.bar_data.length;i++){
        ret.push(this.bar_data[i].name)
      }
      return ret
    },
    length_scale(){
      return d3.scale.linear().domain([0,400]).range([100,this.width /2 ])
    },
    bar_data(){
      let ret = []
      for(let i = 0;i<this.district_list.length;i++){
        let dist = this.district_list[i]
        let dic = new Object()
        dic.name = dist
        let addup = this.show_data.filter(d=>d.district== dist).length
        dic.count = addup
        ret.push(dic)
      }

      return ret.sort((a,b)=>b.count - a.count)
      
    },
    district_list(){
      let se = new Set()
      for(let i = 0;i<this.daily.length;i++){
        se.add(this.daily[i].district)
      }
      return Array.from(se)
    },
    show_data(){
      if(this.cumulated){
        return this.daily.filter(d=>d.date<=this.date_select)
      }
      else{
        return this.daily.filter(d=>d.date==this.date_select)
      }
      

    },  
    date_select(){
      return this.date_list[this.date_index]
    },
    date_list(){
      let se = new Set()
      for(let i = 0;i<this.daily.length;i++){
        se.add(this.daily[i].date)
      }
      return Array.from(se).sort((a,b)=>a-b)
    },
    daily(){
      let lst = []
      for(let i = 0;i<this.event_data.length;i++){
        let tmp_data = this.event_data[i]
        let date = parseInt(tmp_data['24时日期'].substring(0,4) +
                   tmp_data['24时日期'].substring(5,7) + 
                   tmp_data['24时日期'].substring(8,10))
        if(date){
          let new_data = new Object()
          new_data.date = date
          new_data.id = tmp_data['感染者编号']? tmp_data['感染者编号'] : 'Unknown'
          new_data.index = tmp_data['表格ID']
          new_data.daily_index = tmp_data['每日编号']
          new_data.type = tmp_data['未命名1']
          new_data.district = tmp_data['区']
          new_data.address = tmp_data['居住地址']
          new_data.source = tmp_data['上游']? tmp_data['上游'] : 'Unknown'
          new_data.source_address = tmp_data['上游地址']
          new_data.data_problem = tmp_data['数据问题']?tmp_data['数据问题']:'No problem.'
          let space = tmp_data['坐标'].substring(6,tmp_data['坐标'].length-1).split(' ')
          new_data.xy = space.map(x=>parseFloat(x))
          let source_space = tmp_data['上游坐标']? tmp_data['上游坐标'].substring(6,tmp_data['坐标'].length-1).split(' '):0
          new_data.source_xy = source_space? source_space.map(x=>parseFloat(x)):0
          lst.push(new_data)

        }
        
      }

      return lst
    },
    projection(){
      return d3.geo.mercator().center([116.418757,40.47544]).scale(34000 * this.width/1800)
    },
    map_path(){
      return d3.geo.path().projection(this.projection)
    },
    style(){
      return "background-color:"+"black" +";position:absolute;border-radius: 15px;top:10px;border:1.5px solid #0E021B;width:"+this.width+"px;"+"height:"+this.height+ "px;" 
    },
    style2(){
      return "background-color:"+"black" +";position:absolute;border-radius: 15px;top:10px;left:"+(this.width + 15)+";border:1.5px solid #0E021B;width:"+(this.width/2 - 25)+"px;"+"height:"+this.height * 2/3+ "px;" 
    },

    style3(){
      return "background-color:"+"black" +";position:absolute;border-radius: 15px;top:"+(this.height *2/3 + 15) + "px;left:"+(this.width + 15)+";border:1.5px solid #0E021B;width:"+(this.width/2 - 25)+"px;"+"height:"+(this.height/3 - 5)+ "px;" 
    },
  },
  methods:{
    log(){
      console.log(this.event_data)
      console.log(this.daily)
      console.log(this.date_list)
      console.log(this.show_data)
      console.log(this.district_list)
      console.log(this.bar_data)
      console.log(this.line_data)
     
    },
    rank_updater(){
      d3.select("#ranking").selectAll(".bars")
      .data(this.district_list)
      .enter()
      .append("rect")
      .attr("class",'bars')
      .attr("x",20)
      .attr("height", this.height * 2 / 3 / this.district_list.length - 10)
      .attr("stroke-opacity",0)
      d3.select("#ranking").selectAll(".bars")
      .data(this.district_list)
      .transition()
      .duration(800)
      .attr("y",d=>this.rank.indexOf(d) * (this.height * 2 / 3 / this.district_list.length - 5) + 50)
      .attr("stroke",d=>this.color_scale(d))
      .attr("width",d=>this.length_scale(this.bar_data[this.rank.indexOf(d)].count))
      .attr("stroke-opacity",1)
      d3.select("#ranking").selectAll(".texts")
      .data(this.district_list)
      .enter()
      .append("text")
      .attr("class",'texts')
      .attr("fill",'white')
      .attr("font-size",14)
      .attr("text-anchor",'middle')
      .attr("fill-opacity",0)
      d3.select("#ranking").selectAll(".texts")
      .data(this.district_list)
      .transition()
      .duration(800)
      .attr("dx",d=>this.length_scale(this.bar_data[this.rank.indexOf(d)].count) /2 + 20)
      .attr("dy",d=>this.rank.indexOf(d) * (this.height * 2 / 3 / this.district_list.length - 5) + 65)
      .attr("fill-opacity",1)
      .text(d=>d + ' ：' + this.bar_data[this.rank.indexOf(d)].count)
    },
    

  },
  watch:{
    time_flag(val){
      if(val){
        this.timer = setInterval(()=>{
          if(this.date_index < this.date_list.length - 1){
            this.date_index ++ 
          }
          else{
            this.time_flag = false
          }
        },2000)
      }
      else{
        clearInterval(this.timer)
      }
    },
    date_index(){
      this.rank_updater()
    },
    watcher(){
      this.rank_updater()
    }
  
  }
}
</script>

