h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}


input[type="range"]{
    width: 50 px;
    height: 10px;
    top: 65px;
    left: 25px;
    background-color: black ;
    border:1px solid white;
    border-radius: 5px;
    position: absolute;
    -webkit-appearance: none !important; 
    outline: none;
    transition : all 0.3s;
}
input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 20px;
    height: 20px;
    border-radius: 50%;
