    stroke :white;
    border : 2px solid white;
    transition : all 0.3s;
}

.person{
  animation: showDivAni 1.5s ;
  
}
.bars{
  transition: all 0.5s;
}
#move{
  transition:all 0.5s
}

@keyframes showDivAni{
	0%{opacity:0;}
	100%{opacity: 0.7;}
	
}



</style>


